import { BaseValueObject } from '../base';

export class PostalCode extends BaseValueObject<'PostalCode', string> {
  static from(value: string): PostalCode {
    return new PostalCode(value);
  }
  protected validate(value: string): string {
    const trimmed = value.trim();

    if (trimmed.length === 0) {
      throw new Error('PostalCode must not be empty');
    }

    if (trimmed.length !== 7) {
      throw new Error('PostalCode must be 7 characters');
    }

    return value;
  }
}
